.swiper-container {
  --swiper-theme-color: var(--gradient);
  --swiper-navigation-color: var(--grey-text-color);
  --swiper-navigation-size: 11px;
  --swiper-navigation-top-offset: 50%;
  --swiper-navigation-sides-offset: 7px;
  --swiper-pagination-top: 0;
  --swiper-pagination-bottom: 0;
  --swiper-pagination-bullet-size: 24px;
  --swiper-pagination-bullet-width: 24px;
  --swiper-pagination-bullet-height: 24px;
  --swiper-pagination-bullet-horizontal-gap: 4px;
  --swiper-pagination-bullet-inactive-color: transparent;
  --swiper-pagination-bullet-inactive-opacity: 1;
  position: relative;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .swiper-container {
    --swiper-navigation-size: 14px;
    --swiper-navigation-sides-offset: 16px;
    --swiper-pagination-bullet-size: 32px;
    --swiper-pagination-bullet-width: 32px;
    --swiper-pagination-bullet-height: 32px;
    --swiper-pagination-bullet-horizontal-gap: 6px;
  }
}

@media screen and (min-width: 1200px) {
  .swiper-container {
    --swiper-navigation-size: 16px;
    --swiper-navigation-sides-offset: 22px;
    --swiper-pagination-bullet-size: 40px;
    --swiper-pagination-bullet-width: 40px;
    --swiper-pagination-bullet-height: 40px;
    --swiper-pagination-bullet-horizontal-gap: 8px;
  }
}

.swiper-controls {
  width: 200px;
  height: 24px;
  position: absolute;
  bottom: 28px;
  left: calc(50% - 145px);
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .swiper-controls {
    width: 272px;
    height: 32px;
    bottom: 36px;
    left: calc(50% - 352px);
  }
}

@media screen and (min-width: 1200px) {
  .swiper-controls {
    width: 368px;
    height: 40px;
    bottom: 159px;
    left: calc(50% - 572px);
  }
}

.swiper-pagination {
  color: var(--grey-text-color);
}

.swiper-pagination-bullet {
  border: 1px solid var(--grey-text-color) !important;
  font-size: 10px !important;
  line-height: 24px !important;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .swiper-pagination-bullet {
    border-width: 2px;
    font-size: 12px !important;
    line-height: 32px !important;
  }
}

@media screen and (min-width: 1200px) {
  .swiper-pagination-bullet {
    font-size: 14px !important;
    line-height: 40px !important;
  }
}

.swiper-pagination-bullet-active {
  color: var(--main-bg-color) !important;
}

.trends {
  background-color: var(--main-bg-color);
  padding-top: 80px;
  padding-bottom: 60px;
}

.trends-top {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .trends-top {
    margin-bottom: 28px;
  }
}

.trends-title {
  text-transform: uppercase;
  color: var(--primary-text-color);
  font-size: 28px;
  font-weight: 500;
  line-height: 1.17;
}

.trends-link {
  color: var(--accent-color);
  font-size: 28px;
  font-weight: 500;
  line-height: 33px;
}

.trends-link:focus, .trends-link:hover {
  text-shadow: 1px 1px 8px var(--primary-text-color);
  scale: 1.1;
}

.trends-list {
  flex-wrap: wrap;
  gap: 32px 16px;
  display: flex;
}

.trends-card {
  height: 335px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: cover;
  flex-basis: 100%;
  transition: scale .2s linear, box-shadow .2s linear;
  position: relative;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .trends-card {
    height: 335px;
    flex-basis: calc(33.3333% - 10.6667px);
  }
}

@media screen and (min-width: 1200px) {
  .trends-card {
    height: 574px;
    flex-basis: calc(33.3333% - 10.6667px);
  }
}

.trends-card:hover, .trends-card:focus {
  box-shadow: 1px 1px 14px var(--primary-text-color);
  scale: 1.03;
}

.trends-card-about {
  position: absolute;
  bottom: 12px;
  left: 12px;
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .trends-card-about {
    width: 60%;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .trends-card-about {
    width: 130px;
  }
}

@media screen and (min-width: 1200px) {
  .trends-card-about {
    width: 245px;
    bottom: 20px;
    left: 20px;
  }
}

.trends-card-subtitle {
  color: var(--color-title-card);
}

.trends-card-subtitle, .trends-card-meta {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.67;
}

@media screen and (min-width: 1200px) {
  .trends-card-subtitle, .trends-card-meta {
    font-size: 20px;
    line-height: 1.17;
  }
}

.trends-card-meta {
  color: var(--color-subtitle-card);
  text-transform: none;
}

.vote-cinemas {
  position: absolute;
}

@media screen and (min-width: 320px) and (max-width: 480px) {
  .vote-cinemas {
    width: 86px;
    height: 14px;
    bottom: 16px;
    right: 12px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .vote-cinemas {
    width: 66px;
    height: 10px;
    bottom: 16px;
    right: 12px;
  }
}

@media screen and (min-width: 1200px) {
  .vote-cinemas {
    width: 106px;
    height: 18px;
    bottom: 20px;
    right: 20px;
  }
}

