  color: #111;
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: none;
  border-radius: 74px;
  gap: 10px;
  padding: 12px 24px;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.14;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.remind-btn:hover, .remind-btn:focus {
  color: var(--accent-color);
  background: var(--btn-gradient) padding-box, linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%) border-box;
